/* ResultAll.css */

.container {
  max-width: 800px;
  margin: 0 auto;
  padding: 20px;
  background-color: #f2f2f2; /* Light Mint background */
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
  border-radius: 8px;
}

.heading {
  font-size: 24px;
  margin-bottom: 20px;
  color: #103c11; /* Green heading text */

}

table {
  width: 100%;
  border-collapse: collapse;
  margin-top: 20px;
  
}

th, td {
  border: 1px solid #c1d2ca; /* Pastel Green border */
  padding: 12px;
  text-align: left;
}

th {
  background-color: #4CAF50; /* Green header background */
  font-weight: bold;
  color: #122820; /* White header text */
  text-shadow: 1px 1px 1px #1e5e20; /* Adding a subtle text shadow for better visibility */
  font-weight: bolder;

}

tr:hover {
  background-color: #e0f2e9; /* Light Mint on hover */
}

/* Alternating row colors for better readability */
tbody tr:nth-child(even) {
  background-color: #edf7f4; /* Light Mint */
}

tbody tr:nth-child(odd) {
  background-color: #ffffff; /* White */
  text-shadow: #032b04;
}

/* Specific styles for ranks 1, 2, and 3 */
tbody tr:nth-child(1),
tbody tr:nth-child(2),
tbody tr:nth-child(3) {
  color: #234824; /* Alizarin Red text for top 3 ranks */
  font-weight: bold;
  text-shadow: 0.2px 0.1px 0.4px #032b04; /* Adding a subtle text shadow for better visibility */


}

/* Responsive design for larger screens, treating them like a desktop view */
@media screen and (min-width: 768px) {


  
  th,
  td {
    display: table-cell;
  }

  tbody tr {
    display: table-row;
  }

}
